





























































.raw-message {
  display: grid;
  div {
    overflow: auto;
    overflow-y: hidden;
    white-space: nowrap;
  }
}
table.message-fields {
  th {
    padding: 0;
    .letters {
      padding: 0.25em 0.5em;
    }
  }
}

.letters {
  font-family: "Source Code Pro", monospace;
  background: #efefef;
  padding: unset;

  span {
    padding: unset;
    line-height: 25px;
    text-align: center;
    letter-spacing: unset;
    text-rendering: optimizeLegibility;
    white-space: pre;
  }

  i {
    font-style: normal;
    font-weight: 600;
    white-space: nowrap;
    display: inline-block;
    margin-right: 1px;
    padding: 0 5px;
    vertical-align: middle;
    height: 25px;
    width: 20px;
    color: white;
  }
}
